import {
  ButtonHTMLAttributes,
  MouseEventHandler,
  ReactNode,
  useEffect,
  useRef,
  useState,
} from 'react';
import Spinner from '../Spinner';
import styles from './style.module.css';

interface Props extends ButtonHTMLAttributes<HTMLButtonElement> {
  keyboardShortcut?: string | string[];
  delay?: number;
  spinner?: ReactNode;
}

const ResponseButton = ({
  children,
  className,
  keyboardShortcut,
  onClick,
  delay = 0,
  spinner = <Spinner type="spinner" color="#76cdff" width={44} height={44} strokeWidth={8} />,
  ...rest
}: Props) => {
  const [isLoading, setIsLoading] = useState(false);
  const elementRef = useRef<HTMLButtonElement>(null);

  const handleOnClick: MouseEventHandler<HTMLButtonElement> = (e) => {
    if (delay < 1) {
      onClick && onClick(e);
      return;
    }

    setIsLoading(true);

    setTimeout(() => {
      setIsLoading(false);
      onClick && onClick(e);
    }, delay);
  };

  useEffect(() => {
    if (!keyboardShortcut) return;

    const shortcuts = Array.isArray(keyboardShortcut) ? keyboardShortcut : [keyboardShortcut];

    const handleKeyDown = (e: KeyboardEvent) => {
      if (shortcuts.includes(e.key)) {
        elementRef?.current?.click();
      }
    };

    document.addEventListener('keydown', handleKeyDown, false);
    return () => {
      document.removeEventListener('keydown', handleKeyDown, false);
    };
  }, [keyboardShortcut]);

  return isLoading ? (
    <>{spinner}</>
  ) : (
    <button
      ref={elementRef}
      {...rest}
      onClick={handleOnClick}
      className={`${styles['button-77']} ${isLoading ? styles['loading'] : ''} ${className ?? ''}`}
      // disabled={isLoading}
    >
      {children}
    </button>
  );
};

export default ResponseButton;
