import Avatar from 'components/Avatar';
import ResponseButton from 'components/ResponseButton';
import { useState } from 'react';
import { GameResponse, GameResponseOption, Profile } from 'types/configs';
import Spinner from '../Spinner';
import { log } from '../../utils';

interface Props {
  from: Profile;
  response: GameResponse;
  handleResponse: (response: GameResponseOption) => void;
  moduleClasses: (classes: string) => string;
}

const ResponsePromptSingleChoice = ({
  from: { avatar },
  response: { prompt, options },
  handleResponse,
  moduleClasses,
}: Props) => {
  const [isLoading, setIsLoading] = useState(false);

  const getOnClickHandlerForResponse = (response: GameResponseOption) => () => {
    log({ response });
    setIsLoading(true);

    setTimeout(() => {
      setIsLoading(false);
      handleResponse(response);
    }, 800);
  };

  return (
    <div className={moduleClasses('content-item responses')}>
      <div className={moduleClasses('res-container')}>
        <>
          <div className={moduleClasses('res-item res-prompt')}>
            {prompt
              ? prompt
              : options.length > 1
              ? 'Choose one of the following options to respond:'
              : `Click ${options[0].label} to continue\u2026`}
          </div>
          <div className={moduleClasses('res-item res-photo')}>
            <Avatar avatar={avatar} />
          </div>
          {isLoading ? (
            <div className={moduleClasses('res-item response')}>
              <Spinner type="spinner" color="#76cdff" width={44} height={44} strokeWidth={8} />
            </div>
          ) : (
            options.map((response) => (
              <div key={response.id} className={moduleClasses('res-item response')}>
                <ResponseButton id={response.id} onClick={getOnClickHandlerForResponse(response)}>
                  {response.label}
                </ResponseButton>
              </div>
            ))
          )}
        </>
      </div>
    </div>
  );
};

export default ResponsePromptSingleChoice;
