import { SVGProps } from 'react';

interface SpinnerProps<T> extends SVGProps<T> {
  dasharrayLinePerc?: number;
  type?: 'spinner' | 'dots';
}

const Spinner = ({
  width = 80,
  height = 80,
  strokeWidth = 4,
  dasharrayLinePerc = 0.4,
  // color = '#76cdff',
  color = '#0c7dbf',
  // color = '#E25606',
  // color = '#fff',
  dur = '0.5s',
  type = 'spinner',
}: SpinnerProps<SVGElement>): JSX.Element => {
  const radius = 25;
  const circ = 2 * Math.PI * 25;
  const dasharrayLine = dasharrayLinePerc * circ;
  const dasharrayGap = (1 - dasharrayLinePerc) * circ;

  return type === 'spinner' ? (
    // Spinner, see: https://fffuel.co/svg-spinner/
    <svg
      width={width}
      height={height}
      stroke={color}
      strokeWidth={strokeWidth}
      strokeDasharray={`${dasharrayLine} ${dasharrayGap}`}
      viewBox="0 0 100 100"
      xmlns="http://www.w3.org/2000/svg"
    >
      <circle
        cx="50"
        cy="50"
        fill="none"
        r={radius}
        // stroke="#0c7dbf"
        strokeDasharray={circ}
        strokeOpacity="0.25"
        strokeLinecap="round"
      />
      <circle
        cx="50"
        cy="50"
        fill="none"
        r={radius}
        // stroke-width="6"
        // stroke="#E387FF"
        // stroke-dasharray="60 100"
        strokeLinecap="round"
      >
        <animateTransform
          attributeName="transform"
          type="rotate"
          dur={dur}
          values="0 50 50;360 50 50"
          repeatCount="indefinite"
        />
      </circle>
    </svg>
  ) : (
    // Three Dots Bounce, see: https://github.com/dephraiim/react-svg-spinners/blob/main/src/components/ThreeDotsBounce.tsx
    // <svg
    //   width={width}
    //   height={height}
    //   fill={color}
    //   viewBox="0 0 24 24"
    //   xmlns="http://www.w3.org/2000/svg"
    // >
    //   <circle cx="4" cy="12" r="3">
    //     <animate
    //       id="a"
    //       begin="0;b.end+0.25s"
    //       attributeName="cy"
    //       calcMode="spline"
    //       dur={dur}
    //       values="12;6;12"
    //       keySplines=".33,.66,.66,1;.33,0,.66,.33"
    //     />
    //   </circle>
    //   <circle cx="12" cy="12" r="3">
    //     <animate
    //       begin="a.begin+0.1s"
    //       attributeName="cy"
    //       calcMode="spline"
    //       dur={dur}
    //       values="12;6;12"
    //       keySplines=".33,.66,.66,1;.33,0,.66,.33"
    //     />
    //   </circle>
    //   <circle cx="20" cy="12" r="3">
    //     <animate
    //       id="b"
    //       begin="a.begin+0.2s"
    //       attributeName="cy"
    //       calcMode="spline"
    //       dur={dur}
    //       values="12;6;12"
    //       keySplines=".33,.66,.66,1;.33,0,.66,.33"
    //     />
    //   </circle>
    // </svg>
    // Three Dots Fade, see: https://github.com/dephraiim/react-svg-spinners/blob/main/src/components/ThreeDotsFade.tsx
    <svg
      width={width}
      height={height}
      fill={color}
      viewBox="0 0 24 24"
      xmlns="http://www.w3.org/2000/svg"
    >
      <circle cx="4" cy="12" r="3" opacity="1">
        <animate
          id="a"
          begin="0;b.end-0.25s"
          attributeName="opacity"
          dur={dur}
          values="1;.2"
          fill="freeze"
        />
      </circle>
      <circle cx="12" cy="12" r="3" opacity=".4">
        <animate
          begin="a.begin+0.15s"
          attributeName="opacity"
          dur={dur}
          values="1;.2"
          fill="freeze"
        />
      </circle>
      <circle cx="20" cy="12" r="3" opacity=".3">
        <animate
          id="b"
          begin="a.begin+0.3s"
          attributeName="opacity"
          dur={dur}
          values="1;.2"
          fill="freeze"
        />
      </circle>
    </svg>
  );
};

export default Spinner;
