import Avatar from 'components/Avatar';
import Image from 'components/Image';
import { ReactNode } from 'react';
import { ChatMessage, GetChatProfileFn, Image as ImageType } from 'types/configs';

interface Props extends Omit<ChatMessage, 'type'> {
  spinner?: ReactNode;
  getChatProfile: GetChatProfileFn;
  moduleClasses: (classes: string) => string;
  enrichMessage: (message: string) => string;
  onLoadHandler?: React.ReactEventHandler<HTMLImageElement>;
}

const isImage = (
  message: Props['messages'] extends (infer U)[] ? U : never,
): message is ImageType => typeof message === 'object' && message !== null && 'src' in message;

const Messages = ({
  from,
  messages,
  spinner,
  getChatProfile,
  moduleClasses,
  enrichMessage,
  onLoadHandler,
}: Props) => {
  const profile = getChatProfile(from);
  return (
    <div className={moduleClasses('content-item messages')}>
      <div className={moduleClasses('msg-container')}>
        <div className={moduleClasses('msg-item msg-name')}>{profile.name}</div>
        <div className={moduleClasses('msg-item msg-photo')}>
          <Avatar type="npc" avatar={profile.avatar} />
        </div>
        {spinner ? (
          <div className={moduleClasses('msg-item msg')}>{spinner}</div>
        ) : (
          messages.map((message, i) => (
            <div key={i} className={moduleClasses('msg-item msg')}>
              {isImage(message) ? (
                <Image {...message} onLoad={onLoadHandler} />
              ) : (
                enrichMessage(message)
              )}
            </div>
          ))
        )}
      </div>
    </div>
  );
};

export default Messages;
