import { ReactEventHandler } from 'react';
import { ChatMessages, GetChatProfileFn, Profile } from '../../types/configs';
import Messages from '../Messages';
import Response from '../Response';

interface Props {
  enrichMessage: (message: string) => string;
  getChatProfile: GetChatProfileFn;
  messages: ChatMessages[];
  moduleClasses: (classes: string) => string;
  onLoadHandler?: ReactEventHandler<HTMLImageElement>;
  profile: Profile;
}

export const Conversation = ({
  enrichMessage,
  getChatProfile,
  messages,
  moduleClasses,
  onLoadHandler,
  profile,
}: Props) => {
  const newMessageIndex = messages.findLastIndex((m) => m.type === 'response');

  return (
    <>
      {messages.map((message, i) =>
        message.type === 'message' ? (
          <Messages
            {...message}
            key={i}
            enrichMessage={enrichMessage}
            getChatProfile={getChatProfile}
            moduleClasses={moduleClasses}
            onLoadHandler={onLoadHandler}
          />
        ) : (
          <>
            <Response
              key={i}
              from={profile}
              messages={message.messages}
              moduleClasses={moduleClasses}
            />
            {newMessageIndex === i ? (
              <div className={moduleClasses('new-messages-delimiter')}>New Messages</div>
            ) : null}
          </>
        ),
      )}{' '}
    </>
  );
};
